        beskrivelse: "På samme kafé som i fjor sitter du i år igjen. Med et sterkt håp om at du nok en gang skal få oppleve adventens magi, og dra ut på en ny reise. Inn døren på kaféen kommer en ener du drar kjensel på og som sjefer der han tidligere triumferte. Personen viser deg bilde fra en av de farligste arbeidsplassene han har hatt. Den er svart, men det er ikke hos frisøren. En av hans verste dager på jobb var akkurat her. Selv om han da kom seg helskinnet forbi både musefella og karusellen, gikk det fryktelig galt ved kanten av husfjellet. Han forteller videre at det blir utført ulike former av jobben her, og at i hans spesialitet har han vært best på jobb tre ganger. Du skjønner at du har fått ditt første reisemål og vil komme deg av gårde, men før du drar videre gir han deg adgang til det han er aktuell med nå om dagen. Kanskje du kan benytte deg av den etter du er hjemme igjen fra reisen? Hvilken by skal vi til?",
