const Div = styled.div`
  margin-left: auto;
  margin-right: auto;
  max-width: 350px;
  margin-bottom: 15px;
`

const Bordered = styled.div`
  border: 1px solid white;
  margin-top: 20px;
  padding-bottom: 20px;
  background-color: #43586b;
`
const Grid = styled.div`
  display: flex;
  flex-wrap: wrap;

  margin: auto;
  margin-bottom: 50px;
`
const Oppgave = styled.div`
  padding: 0px 20px 5px 20px;
  text-align: justify;
`
const Lenke = styled.div`
  padding-bottom: 10px;
`
const Lenker = styled.div`
  margin-top: -30px;
`
const Item = styled.div`
    flex: 1 0 21%; /* explanation below */
    margin: 5px;
    height: 75px;
    background-color: grey;
    text-align: center;
    color: white;
    font-size: 36px;
    
  line-height: 75px;
`
export const Hjem = () => {
    const d = new Date();
    const klokkeslett = d.getHours()
    const dato = (d.getDate() > 24) ? 0 : d.getDate();
    const [oppgaveValgt, setOppgaveValgt] = useState<number>(0)

    return (
        <Div>
            {oppgaveValgt === 0 && (
                <div>
                    <h3>Velg en luke</h3>
                    <Grid>
                        {data.map((element) => (
                            <Item
                                onClick={() => { if ((element.dag <= dato)) { setOppgaveValgt(element.dag) } }}
                                style={{
                                    backgroundColor: (element.dag === dato) ? "#fedfb0" : ((element.dag < dato) ? "#344d2f" : "#5c1111"),
                                    color: ((element.dag < dato) ? "#e9e5e1" : "black"),
                                    opacity: ((element.dag > dato) ? 0.15: 1),
                                    cursor:  ((element.dag <= dato) ? "pointer" : "default")
                                }}
                            >
                                {element.dag}
                            </Item>
                        ))}
                            </Grid>
                    <Lenker>        
                    {dato !== 1 && (
                        <Lenke>
                            <Link to="/toppliste">Sjekk topplisten her</Link>
                        </Lenke>
                    )}
                    <Lenke>
                        <a href="https://www.facebook.com/groups/131845715156441">Gå til Facebook-siden</a>
                    </Lenke>
                    </Lenker>        
                </div>
            )}
            {oppgaveValgt !== 0 && (
                <div>
                    <Link onClick={() => setOppgaveValgt(0)} to={""}>Gå tilbake til oppgavene</Link>
                    <Bordered>
                        <h3>Oppgave for {oppgaveValgt}. Desember:</h3>
                        <Oppgave>{data.find((element) => element.dag === oppgaveValgt)?.beskrivelse}</Oppgave>
                        {oppgaveValgt < dato && (
                            <div>
                                <h3>Svar:</h3>
                                <p>{data.find((element) => element.dag === oppgaveValgt)?.svar}</p>
                            </div>
                        )}
                        {oppgaveValgt === dato && (
                            <div>
                                <Oppgave>
                                    {klokkeslett >= 18 && (
                                        <p><i>Hint: {data.find((element) => element.dag === oppgaveValgt)?.hint}</i></p>
                                    )}
                                    {klokkeslett < 18 && (
                                        <p><i>Står du fast? Hint kommer klokken 18:00</i></p>
                                    )}
                                </Oppgave>
                                <h3>Har du funnet løsningen?</h3>
                                <a href="https://forms.gle/z25GJuVRQbXFwzAu8">Send inn svaret ditt her</a>
                            </div>
                        )}
                    </Bordered>
                </div>
            )}
        </Div>
    )
}